@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&family=Raleway:wght@700&display=swap");

body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  font-size: 15px;
  font-family: "Montserrat", sans-serif;
}

ul,
li,
section,
div {
  /* border: 1px solid red; */
}

a {
  text-decoration: none;
  color: inherit;
  font-weight: bold;
}
h1 {
  font-size: 2rem;
  font-family: "Raleway", sans-serif;
  text-align: center;
}
main {
  display: flex;
  justify-content: center;
  margin: 10px;
}

section.todolist {
  display: flex;
  flex-direction: column;

  width: 100%;
}

nav {
  width: 100%;
  border-bottom: 1px solid #ccc;
}
nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}
nav ul li {
  flex: 1;
  margin: 0 30px;
  text-align: center;
  padding-bottom: 10px;
}
nav ul li.active {
  border-bottom: 4px solid #6da6f2;
}

form {
  display: flex;
  padding: 10px 0;
}
form input {
  font-family: "Montserrat", sans-serif;
}
form button {
  margin-left: 10px;
}

.singleJob {
  display: flex;
  align-items: center;
}
.singleJob label {
  font-weight: bold;
  cursor: pointer;
  flex-grow: 1;
}
.singleJob label.finished {
  text-decoration: line-through;
}

.singleJob button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.deleteRow {
  display: flex;
  justify-content: center;
}
.deleteRow button {
  display: flex;
  align-items: center;
}

/*desktop responsive*/
@media only screen and (min-width: 768px) {
  section.todolist {
    width: 500px;
  }
}
